#benefits {
    padding: 4rem 0;
}

@media screen and (max-width: 1200px) {
    .benefits {
        &__floating-quote {
            width: 35%;
        }
    }
}

@media screen and (max-width: 575px) {
    .benefits {
        &__floating-quote {
            display: none;
        }
    }
}